.wrapper {
  max-width: 941px;
  width: 100%;
  border-radius: 5px;
  background: rgb(255, 255, 255);
  width: 100%;
  margin: 0 auto;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  .left {
    flex: 0 1 80%;
  }
  .top {
    display: flex;
    align-items: center;
    .title {
      color: rgb(24, 144, 255);
      font-size: 20px;
      font-weight: 400;
      line-height: 28px;
      letter-spacing: 0%;
      text-align: left;
      margin-right: 13px;
    }
    .favourited {
      background-size: contain;
      background-color: white;
      background-repeat: no-repeat;
      display: inline-block;
      margin-left: 13px;
      width: 16px;
      height: 14.23px;
      vertical-align: baseline;
      margin-right: 4px;
      align-self: center;
    }
    .description {
      color: rgba(0, 0, 0, 0.75);
      font-family: Inter;
      font-size: 12px;
      line-height: 22px;
      letter-spacing: 0px;
    }
    .heartWrapper {
      display: flex;
      gap: 4px;
    }
  }
  .tags {
    display: flex;
    gap: 8px;
    margin-bottom: 4px;
    .tag {
      border: 1px solid rgba(0, 0, 0, 0.5);
      border-radius: 2px;
      background: rgb(255, 255, 255);
      font-size: 12px;
      font-weight: 400;
      line-height: 15px;
      padding: 3px;
    }
  }
  .right {
    display: flex;
    gap: 12px;
    flex: 0 1 20%;
    justify-content: flex-end;
    .info {
      display: flex;
      flex-direction: column;
      align-items: end;
      .username {
        font-size: 18px;
        line-height: 28px;
        letter-spacing: 0%;
      }
      .createdAt {
        color: rgba(0, 0, 0, 0.5);
        font-size: 12px;
        line-height: 22px;
        letter-spacing: 0px;
      }
    }

    .image {
      width: 46px;
      height: 46px;
      border-radius: 50%;
    }
  }
}
