.container {
  font-family: Roboto;
  color: rgb(38, 38, 38);
  letter-spacing: 0%;
  box-sizing: border-box;
  border: 1px solid rgb(217, 217, 217);
  border-radius: 6px;
  box-shadow:
    0px 0.61px 2.93px 0px rgba(0, 0, 0, 0.02),
    0px 1.46px 7.05px 0px rgba(0, 0, 0, 0.03),
    0px 2.75px 13.27px 0px rgba(0, 0, 0, 0.04),
    0px 4.91px 23.68px 0px rgba(0, 0, 0, 0.04),
    0px 9.19px 44.28px 0px rgba(0, 0, 0, 0.05),
    0px 22px 106px 0px rgba(0, 0, 0, 0.07);
  background: rgb(255, 255, 255);
  width: 384px;
  // height: 599.5px;
  margin: 0 auto;

  .info {
    display: flex;
    flex-direction: column;
    gap: 12px;
    text-align: left;
    justify-content: center;
    padding: 48px 32px 48px 32px;
    .title {
      // color: rgb(38, 38, 38);
      // font-family: Roboto;
      font-size: 20px;
      font-weight: 500;
      line-height: 28px;
      // letter-spacing: 0%;
      text-align: center;
      margin-bottom: 9px;
    }
    .text {
      display: block;
      color: rgb(38, 38, 38);
      //   font-family: Roboto;
      font-size: 14px;
      font-weight: 400;
      line-height: 22px;
    }
    .input {
      width: 320px;
      height: 40px;
      display: block;
      border: 1px solid rgb(217, 217, 217);
      border-radius: 4px;
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      padding: 8px 12px;
      color: rgb(38, 38, 38);
    }
    .agreement {
      margin-top: 8px;
      line-height: 22px;
    }

    .button {
      width: 319px;
      height: 40px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      padding: 8px 16px 8px 16px;
      border-radius: 4px;
      background: rgb(24, 144, 255);
      color: rgb(255, 255, 255);
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      text-align: center;
      margin-bottom: 8px;
      margin-top: 9px;
    }
    .question {
      color: rgb(140, 140, 140);
      text-align: center;
      .link {
        color: rgb(24, 144, 255);
      }
    }
    .error {
      color: rgb(245, 34, 45);
      font-size: 14px;
      line-height: 22px;
      letter-spacing: 0%;
    }
    .inputError {
      border: 1px solid red;
    }
  }
}
